            // ]
        }
    },
    methods:{
        gerarContrato(){
            if(this.Quantdeparcelas>420)
                return this.alerta="Numero de parcelas acima do permitido" 
            if(!this.Valordocontrato)
                return this.alerta="valor não informado"
            if(!this.Quantdeparcelas)
                return this.alerta="Parcela não informada"
            if(!this.Taxadocontrato)
                return this.alerta="Taxa não informada"
            if(!this.Datadaprimeiraparcela)
                return this.alerta="Informe uma data"
            if(this.Empresa>1)
                return this.alerta="Existre uma simulação de Fno já lançado, a pagina será recarregada"
                //window.location.reload()

             
            //this.alerta=""   
        }

    }

